  import urls from '../../utils/urls.js'
  import {pathToBase64, base64ToPath} from '../../utils/base64.js'
  export default {
    props: ['isShow', 'title', 'logo', 'sex',],
    data () {
      return {
		isLogin: this.$store.state.isLogin
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.userInfo
      }
    },
    methods: {
      getPhoto: function () {
        // let id = JSON.parse(uni.getStorageSync('userInfo')).id
		let id = '';
		if(this.isLogin){
			id = JSON.parse(uni.getStorageSync('userInfo')).id
		}else{
			 id = 0
		}
        uni.chooseImage({
        	count: 1,
          sourceType: ['album'],
          success:(res) => {
            pathToBase64(res.tempFilePaths[0])
            .then(base64 => {
              // console.log(base64)
              uni.request({
                url: urls.HTTP + '/user/uploadheadimg',
                method: 'POST',
                name: 'file',
                data: {
                  uid: id,
                  image: base64
                },
                success: (res) => {
                  console.log('上传头像', res)
                  if (res.data.code === 0) {
                    this.$store.dispatch('userInfo', res.data.user)
                    uni.setStorageSync('user', res.data.user)
                  } else {
                    uni.showToast({
                      title: res.data.error,
                      icon: 'none'
                    })
                  }
                }
              })
            })
          }
        })
      },
      getCamera: function () {
        // let id = JSON.parse(uni.getStorageSync('userInfo')).id
		let id = '';
		if(this.isLogin){
			id = JSON.parse(uni.getStorageSync('userInfo')).id
		}else{
			 id = 0
		}
        uni.chooseImage({
        	sourceType: ['camera'],
          success: (res) => {
            let path = res.tempFilePaths[0]
          	uni.saveImageToPhotosAlbum({
          		filePath: path,
              success: () => {
                pathToBase64(path)
                .then(base64 => {
                  uni.request({
                    url: urls.HTTP + '/user/uploadheadimg',
                    method: 'POST',
                    name: 'file',
                    data: {
                      uid: id,
                      image: base64
                    },
                    success: (res) => {
                      if (res.data.code === 0) {
                        this.$store.dispatch('userInfo', res.data.user)
                        uni.setStorageSync('user', res.data.user)
                      } else {
                        uni.showToast({
                          title: res.data.error,
                          icon: 'none'
                        })
                      }
                    }
                  })
                })
              }
              
          	})
          }
        })
      },
      chooseSex: function (s) {
        // var id = JSON.parse(uni.getStorageSync('userInfo')).id
		let id = '';
		if(this.isLogin){
			id = JSON.parse(uni.getStorageSync('userInfo')).id
		}else{
			 id = 0
		}
        uni.request({
          url: urls.HTTP + '/user/edituser',
          method: 'POST',
          data: {
            id: id,
            sex: s
          },
          success: (res) => {
            // console.log('修改性别', res)
            this.$store.dispatch('userInfo', res.data.user)
          }
        })
        this.$emit('getSex', s)
        this.$emit('closeDialog', false)
      },
      closeDialog: function () {
        this.$emit('closeDialog', false)
      }
      
    }
  }
